.track {
    padding: 20px;
    width: 100%;
    background-color: #f6f6f6;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 1px 2px rgba(0,0,0,.1);
    position: relative;
    cursor: pointer;
    margin-bottom: 16px;
}

.track:nth-last-child(1) {
    margin-bottom: 0;
}

.track::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    box-shadow: 1px 4px 10px -8px rgba(0,0,0,.4);
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    border-radius: 8px;
}

.track:hover::after {
  opacity: 1;
}

.track > h4 {
    font-weight: 700;
}

.track > .track__cover {
    width: 48px;
    height: 48px;
    background-position: center;
    background-size: cover;
    margin-right: 12px;
    background-color: rgba(0, 0, 0, .4);
}

.track__author {
    font-weight: 400 !important;
}

.track__info {
    flex: 1;
}

.track__right {
    display: flex;
    align-items: center;
}

.track__right > .MuiSvgIcon-root {
    font-size: 32px;
    color: gray;
}