.sidebar {
    flex: 0.25;
    display: flex;
    flex-direction: column;
    min-width: 400px;
    border-right: 1px solid lightgray;
}

.sidebar__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid lightgray;
}

.sidebar__header>h2{
    color: gray;
}

.sidebar__header>.sidebar__headerSocial {
    min-width: 110px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sidebar__albums {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: #f6f6f6;
}