.tracks {
    display: flex;
    flex-direction: column;
    flex: 0.75;
}

.tracks__header {
    display: flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid lightgray;
    color: gray;
}

.tracks__header>.MuiSvgIcon-root {
    margin: 12px;
    margin-right: 28px;
}

.tracks__body {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 16px;
    overflow: auto;
}

.tracks__footer {
    display: flex;
    flex-direction: column;
    height: 100px;
    color: gray;
    background: #fff;
    border-top: 1px solid lightgray;
}

.tracks__footer > .tracks__footerBar {
    display: flex;
    height: 10px;
    background: rgb(226, 226, 226);
}

.tracks__footerControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 10px;
    padding: 0 12px;
    min-width: 180px;
}

.tracks__footerControl > .MuiSvgIcon-root {
    font-size: 32px;
    margin: 0 4px;
    cursor: pointer;
}

.tracks__footerControl > .MuiSvgIcon-root:hover {
    color: rgb(107, 107, 107);
}

.tracks__footerInfoName {
    margin-top: 20px;
    align-self: flex-start;
    margin-left: 8px;
}

.tracks__footerInfoName > h3 > .MuiSvgIcon-root {
    margin-left: 6px;
    font-size: 16px;
}

.tracks__footerInfoName > h3 {
    font-size: 14px;
    display: flex;
    align-items: center;
    font-weight: 600;
}

.tracks__footerBarProgress{
    background: rgba(255, 79, 79, 0.4);
    height: 100%;
    width: 0%;
}

.tracks__footerInfo {
    flex: 1;
    display: flex;
    align-items: center;
    background: #fff;
}

.tracks__footerInfo > .tracks__footerInfoCover {
    width: 48px;
    height: 48px;
    background: rgba(0, 0, 0, .4);
    background-size: cover;
    background-position: center;
}