.sidebar__albumsAlbum {
    padding: 20px;
    background: #fff;
    color: rgba(0, 0, 0, .5);
    border-bottom: 1px solid rgb(236, 236, 236);
    cursor: pointer;
}

.sidebar__albumsAlbum:hover {
    background: rgb(218, 218, 218);
    color: rgba(0, 0, 0, .6);
}

.sidebar__albumsAlbumContainer {
    display: flex;
    align-items: center;
}


.sidebar__albumsAlbumContainer>h3 {
    margin-left: 16px;
}

.sidebar__albumsAlbumContainerRight {
    margin-left: auto;
    display: flex;
    align-items: center;
}

.feat {
    color: rgba(0, 0, 0, .2);
    font-size: 14px;
    font-weight: 600;
    display: flex;
    align-items: center;
}

.feat .MuiSvgIcon-root{
    font-size: 14px;
    margin-left: 4px;
}